.order {
    display: flex;
    justify-content: space-between;
    margin: 1rem ;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ccc;
  }
  
  .order h3 {
    margin: 0 0 0.25rem 0;
  }

  .order h4 {
    margin: 0.25rem 0.25rem 0.25rem 0;
  }
  
  .address {
    font-style: italic;
  }
  
  .price {
    margin-top: 0.25rem;
    margin-left: 2.5rem;
    font-weight: bold;
    color: #ad5502;
    font-size: 1.25rem;
  }

  .order button {
    font: inherit;
    cursor: pointer;
    background-color: #8a2b06;
    border: 1px solid #8a2b06;
    color: white;
    padding: 0.25rem 2rem;
    border-radius: 20px;
    font-weight: bold;
    margin-top: 1rem;
  }
  
  .order button:hover,
  .order button:active {
    background-color: #641e03;
    border-color: #641e03;
  }

table  thead{
    background-color: #8a2b06;
    color: white;
    font-weight: bold;

}

table tbody tr:nth-child(even) {
    background-color: #e2c1b3;
    border-bottom: 1px solid #d4b8ab;
}

table {
    border-collapse: collapse;
    border: 1px solid #d4b8ab;
    width: 100%;
}

table th {
    padding: .5rem;
}

table td {
  padding: .5rem .5rem .5rem 4rem;

}
